<template>
  <div class="bookrackbooks" @click="navigateToBook(item.index,item.id)">
    <div class="cover">
      <img :src="'http://statics.zhuishushenqi.com'+item.cover" alt />
    </div>
    <div class="right">
      <h4 class="name">{{item.title}}</h4>
      <p class="count">{{(Number(item.index) + 1)+"章/"+item.chaptersCount+"章"}}</p>
    </div>
  </div>
</template>

<script>
export default {
  props: ["item"],
  methods: {
    navigateToBook(chapter_index, id) {
      this.$router.push({
        path: "/book",
        query: {
          chapter_index,
          id,
        },
      });
    },
  },
};
</script>

<style lang="less">
.bookrackbooks {
  overflow: hidden;
  .cover {
    width: 68px;
    height: 88px;
    border-radius: 5px;
    float: left;
    margin-left: 10px;
    margin-top: 10px;
    margin-bottom: 10px;
    overflow: hidden;
  }
  .right {
    width: calc(100vw - 135px);
    float: left;
    margin-left: 15px;
    margin-right: 10px;
    margin-top: 20px;
    .name {
      font-size: 16px;
      font-weight: 400;
      display: -webkit-box;
      -webkit-line-clamp: 1;
      -webkit-box-orient: vertical;
      word-break: break-all;
      text-overflow: ellipsis;
      overflow: hidden;
    }
    .count {
      font-size: 12px;
      margin: 7px 0;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      word-break: break-all;
      text-overflow: ellipsis;
      overflow: hidden;
      color: #989898;
    }
  }
}
</style>