<template>
  <div class="bookrack">
    <BooksManagement v-if="isShowBooksManagement" />
    <template v-else>
      <TopSearch :category="bookrack" />
      <AppNav />
      <div class="managementbox">
        <div class="clickmanagement" @click="isShow">
          书籍管理
          <div class="managemenicon">
            <img src="../assets/Category/managemenicon.png" alt />isShowBooksManagement
          </div>
        </div>
      </div>
      <ul v-if="!parseBookrackList.length=='0'">
        <li v-for="(item, index) in parseBookrackList" :key="index">
          <BookrackBooks :item="item" />
        </li>
      </ul>
      <div v-if="parseBookrackList.length=='0'" class="aaa">书籍没有书籍...</div>
    </template>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";

import TopSearch from "@/components/TopSearch.vue";
import AppNav from "@/components/AppNav.vue";
import BookrackBooks from "@/components/BookrackBooks.vue";
import BooksManagement from "@/components/BooksManagement.vue";
// import Test from "@/components/Test.vue";

export default {
  data() {
    return {
      bookrack: "书架",
      // isShowBooksManagement: false,
      // chaptersCount
    };
  },
  created() {
    this.$store.commit("storageBookrack", localStorage.getItem("Bookrack"));
  },
  methods: {
    ...mapMutations(["trueBooksManagement", "falseBooksManagement"]),
    isShow() {
      // this.isShowBooksManagement = true;
      this.trueBooksManagement();
    },
  },
  components: {
    TopSearch,
    AppNav,
    BookrackBooks,
    BooksManagement,
    // Test,
  },
  computed: {
    ...mapState(["BookrackList", "isShowBooksManagement"]),
    parseBookrackList() {
      return JSON.parse(this.BookrackList);
    },
    aaa() {
      return !this.parseBookrackList.length == "0";
    },
  },
  activated() {
    this.falseBooksManagement();
  },
};
</script>

<style lang="less">
.bookrack {
  background: #f6f7fb;
  min-height: 92vh;
  .managementbox {
    width: 100vw;
    background: #f6f7fb;
    position: fixed;
    top: 46px;
    left: 0;
  }
  .clickmanagement {
    position: relative;
    background: white;
    width: 100px;
    height: 55px;
    // background: #000;
    margin: 5px 0 5px 0;
    margin-left: 15px;
    border-radius: 7px;
    padding: 5px 0 0 5px;
    overflow: hidden;
    font-weight: 550;
    font-size: 14px;
    .managemenicon {
      position: absolute;
      bottom: -20px;
      right: -20px;
      width: 50px;
      height: 50px;
      background: #fcbbc0;
      border-radius: 15px;
      padding-top: 5px;
      padding-left: 5px;
      & > img {
        width: 20px;
        height: 20px;
      }
    }
  }
  & > ul {
    margin: 0 15px 0 15px;
    margin-bottom: 50px;
    margin-top: 75px;
    li {
      background: white;
      margin-bottom: 10px;
      border-radius: 10px;
    }
  }
  & > .aaa {
    margin-top: 75px;
    text-align: center;
  }
}
</style>