<template>
  <div class="booksmanagement">
    <van-nav-bar title="书架管理" left-arrow @click-left="onClickLeft" fixed />
    <van-checkbox-group v-model="result" ref="checkboxGroup">
      <van-cell-group>
        <van-cell
          v-for="(item, index) in parseBookrackList"
          clickable
          :key="index"
          @click="toggle(index)"
        >
          <template #right-icon>
            <div class="cover">
              <img :src="'http://statics.zhuishushenqi.com'+item.cover" alt />
            </div>
            <div class="right">
              <h4 class="name">{{item.title}}</h4>
              <p class="count">{{("已读"+(Number(item.index) + 1))+"章"}}</p>
            </div>
            <van-checkbox :name="item" ref="checkboxes" />
          </template>
        </van-cell>
      </van-cell-group>
    </van-checkbox-group>
    <div class="bottom">
      <div class="checkall" @click="ckickcount">{{all}}</div>
      <div class="delete" @click="deleteResult">{{"删除("+result.length+")"}}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from "vuex";
import { mapMutations } from "vuex";

export default {
  data() {
    return {
      result: [],
      all: "全选",
      count: 0,
      resultId: [],
    };
  },
  computed: {
    ...mapState(["BookrackList"]),
    parseBookrackList() {
      return JSON.parse(this.BookrackList);
    },
  },
  methods: {
    ...mapMutations(["trueBooksManagement", "falseBooksManagement"]),
    onClickLeft() {
      this.falseBooksManagement();
    },
    toggle(index) {
      this.$refs.checkboxes[index].toggle();
    },
    checkAll() {
      this.$refs.checkboxGroup.toggleAll(true);
      this.all = "取消全选";
    },
    toggleAll() {
      this.$refs.checkboxGroup.toggleAll();
      this.all = "全选";
    },
    ckickcount() {
      this.count++;
    },
    deleteResult() {
      if (this.result.length > 0) {
        let newArr1 = [];
        let stringify = null;
        this.parseBookrackList.forEach((item) => {
          if (!this.resultId.includes(item.id)) {
            newArr1.push(item);
          }
        });
        stringify = JSON.stringify(newArr1);
        localStorage.setItem("Bookrack", stringify);
        this.$store.commit("storageBookrack", stringify);
      }
    },
  },
  watch: {
    count: function () {
      if (this.count % 2 == 0) {
        this.toggleAll();
      }
      if (this.count % 2 == 1) {
        this.checkAll();
      }
    },
    result() {
      if (this.result.length > 0) {
        this.resultId = [];
        for (let index = 0; index < this.result.length; index++) {
          this.resultId.push(this.result[index].id);
        }
      } else {
        this.resultId = [];
      }
    },
  },
};
</script>

<style lang="less">
.booksmanagement {
  background: white;
  width: 100vw;
  min-height: 92vh;
  .van-checkbox-group {
    margin-top: 46px;
    margin-bottom: 50px;
    .van-cell-group {
      .van-cell {
        overflow: hidden;
        .cover {
          width: 68px;
          height: 88px;
          border-radius: 5px;
          float: left;
          // margin-left: 10px;
          margin-top: 10px;
          margin-bottom: 10px;
          overflow: hidden;
        }
        .right {
          width: calc(100vw - 145px);
          float: left;
          margin-left: 10px;
          // margin-right: 10px;
          margin-top: 20px;
          .name {
            font-size: 16px;
            font-weight: 400;
            display: -webkit-box;
            -webkit-line-clamp: 1;
            -webkit-box-orient: vertical;
            word-break: break-all;
            text-overflow: ellipsis;
            overflow: hidden;
          }
          .count {
            font-size: 12px;
            margin: 7px 0;
            display: -webkit-box;
            -webkit-line-clamp: 2;
            -webkit-box-orient: vertical;
            word-break: break-all;
            text-overflow: ellipsis;
            overflow: hidden;
            color: #989898;
          }
        }
      }
    }
  }
  .bottom {
    position: fixed;
    bottom: 0;
    left: 0;
    height: 50px;
    background: white;
    width: 100vw;
    .delete {
      width: 50vw;
      float: left;
      height: 100%;
      line-height: 50px;
      text-align: center;
      border-top: 1px solid #ccc;
      color: #46a9fc;
    }
    .checkall {
      width: 50vw;
      float: left;
      height: 100%;
      line-height: 50px;
      text-align: center;
      border-top: 1px solid #ccc;
      border-right: 1px solid #ccc;
      color: #999;
    }
  }
}
</style>